.admin {
    padding-top: 40px;
    padding-bottom: 40px;
}

.admin__container.log {
    max-width: 320px;
    gap: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.admin__login,
.admin__password {
    font-size: 24px;
    font-weight: 300;
    width: 100%;
    padding: 8px 40px 8px 24px;
    background-color: transparent;
    border: 1px solid #222021;
    color: #222021;
}

.admin__login {
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
}

.admin__password {
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
}

.admin__btn {
    color: #222021;
    background-color: #FEE400;
    width: 120px;
    padding: 10px 0;
    border-radius: 48px;
    border: 2px solid transparent;
    cursor: pointer;
    transition: all .3s ease-in-out;
}

@media (hover:hover) {
    .admin__btn:hover {
        background-color: transparent;
        border: 2px solid #222021;
        color: #222021;
    }
}

.items-list {
    display: flex;
    flex-direction: column;
    align-items: start;
    margin: 0 auto;
    margin-bottom: 20px;
}

.items-list__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.admin__form {
    max-width: 320px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}

.admin__form input,
.admin__images {
    font-size: 24px;
    font-weight: 300;
    width: 100%;
    margin-bottom: 15px;
    padding: 10px 24px 10px 24px;
    background-color: transparent;
    border: 1px solid #222021;
    color: #222021;
}

.admin__images {
    cursor: pointer;
    display: flex;
    align-items: start;
    color: #222021;
}

.admin__input--ru {
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
}

.admin__category {
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 24px;
}

.admin__preview {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 5px;
    border: 1px solid #222021;
    margin-bottom: 20px;
    width: 100%;
}

.admin__preview div {
    height: 80px;
    position: relative;
}

.admin__preview button {
    position: absolute;
    top: 0;
    left: 0;
}

.admin__preview div img {
    width: 100%;
    height: 100%;
}

.admin__submit {
    color: #222021;
    background-color: #FEE400;
    width: 190px;
    padding: 20px 0;
    border-radius: 48px;
    border: 2px solid transparent;
    cursor: pointer;
    transition: all .3s ease-in-out;
}

@media (hover:hover) {
    .admin__submit:hover {
        background-color: transparent;
        border: 2px solid #222021;
        color: #222021;
    }
}